.login{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.login__container{
    width: calc(70%);
    height: calc(70%);
    margin: auto;
    background-color: rgb(233, 231, 231);
}

.login__logo{
    width: 250px;
    object-fit: contain;
}

.login__containerDetail{
    display: flex;
    flex-direction: column;
    width: 40%;
    margin: auto;
}
.login__containerDetail > form > h4{
    text-align: left;
}
.login__containerDetail > form > input {
    width: 100%;
    height: 30px;
    font-size: 20px;
    padding-left: 10px;
    border-radius: 10px;
}
.login__containerDetail > form > button {
    width: 50%;
    height: 30px;
    background-color: rgb(250, 216, 216);
    border-radius: 20px;
    margin: 20px 0;
    cursor: pointer;
    transition: transform 450ms;
}
.login__containerDetail > form > button:hover{
    background-color: rgb(253, 209, 216);
    transform: scale(1.1);
}

.registerButton{
    width: 50%;
    height: 30px;
    background-color: rgb(250, 216, 216);
    border-radius: 20px;
    margin: auto;
    cursor: pointer;
}

.registerButton:hover{
    transform: scale(1.1);
    transition: transform 450ms;    
    background-color: rgb(236, 195, 202);
}
